@import url(../../../Fonts/Workbench/Workbench.css);

.hero-container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1;
  grid-column-gap: 10px;
  grid-row-gap: 0px;
  padding: 2%;
}
.workbench-style {
  font-family: "workbench", sans-serif;
  font-weight: 100 !important;
}
.welcome-container-left {
  grid-area: 1 / 1 / 2 / 2;
}
.welcome-container-right {
  grid-area: 1 / 2 / 2 / 3;
  justify-self: center;
}

.image-container {
  justify-self: center;
  border-radius: 50%;
}

.propic-image {
  aspect-ratio: 1;
  width: 50vh;
  border-radius: 50%;
  margin: 10px;
  animation: radius 8s linear infinite;
  box-shadow: 0px 0px 23px rgb(148 0 214 / 67%);
}

.cta {
  text-align: center;
  padding: 6%;
}

@keyframes radius {
  0%,
  100% {
    border-radius: 50% 50% 60% 40% / 45% 35% 60% 50%;
  }
  33% {
    border-radius: 45% 55% 50% 50% / 55% 45% 50% 45%;
  }
  66% {
    border-radius: 40% 60% 40% 60% / 50% 45% 55% 50%;
  }
}

@media screen and (max-width: 900px) {
  .page-container {
    margin-top: calc(var(--nav-height) + 4vh);
  }
  .welcome-container-left h1 {
    text-align: center;
  }
  .welcome-container-left p {
    text-align: justify;
  }
}
@media screen and (max-width: 768px) {
  .navbar-container {
    flex-direction: column;
    align-items: flex-start;
    gap: 0;
    width: 100%;
    height: auto;
    padding: 10px;
    background: rgb(47 47 47 / 80%);
  }

  .nav-toggle {
    display: block;
    cursor: pointer;
    position: relative;
    top: 0;
    right: 0;
    z-index: 1001;
  }

  .expanded .nav-toggle {
    position: absolute;
    top: 2vh;
    right: 2vh;
  }
  .nav-link {
    display: none;
    width: 100%;
    padding: 10px 0;
    text-align: center;
  }

  .navbar-container.expanded .nav-link {
    display: block;
  }

  .nav-toggle span {
    display: block;
    width: 25px;
    height: 3px;
    margin: 5px;
    background: white;
  }

  .hero-container {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 1vh;
  }
  .propic-image {
    width: 28vh;
    margin-top: 5vh;
  }
}

@media screen and (min-width: 769px) and (max-width: 900px) {
  .hero-container {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    gap: 6vh;
  }
  .propic-image {
    width: 40vh;
  }
}
